.main-slider {
  background-color: rgba(0, 0, 0, .7);
  margin: 0 auto;
  width: 60%;
  height: 70vh;
  border: 5px solid grey;
  outline: none;

  .slick-list {
    margin: 0 auto;
    height: 100%;
    max-width: 100%;
    outline: none;

    .slick-track {
      height: 100%;
      margin: 0 auto;
      outline: none;

      .slick-slide {
        margin: 0 auto;
        height: 100%;
        outline: none;

        .slider-img {
          display: block;
          max-width: 100%;
          height: 100%;
          margin: 0 auto;
          padding: 30px 50px;
          outline: none;
        }
      }
    }
  }

}

.slick-arrow {
  position: absolute;
  top: 45%;
  background: transparent;
  height: 50px;
  width: 50px;
  color: transparent;
  font-size: 0;
  outline: none;
}

.slick-next {
  right: -7%;
}

.slick-prev {
  left: -7%
}


.slick-dots {
  margin-top: 2vh;

  li {
    display: inline-block;
    padding: 0 0.3vw;

    button {
      color: #000;
      font-weight: bold;
      font-size: 0.8rem;
      background: #ff8d3e;
      border: 4px solid grey;
      outline: none;
      width: 28px;
      height: 28px;

      &:hover{
        cursor: pointer;
      }
    }
  }
}