.login-register {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 12vh;
  margin: auto;
  width: 400px;
  height: 390px;
  background-color: #4a4a4a;

  form {
    border: 12px solid #4a4a4a;
    background: linear-gradient(#ff884a, #dd521b);
    padding: 20px;
    display: flex;
    flex-flow: column wrap;
    font-size: 1.4rem;
    margin-bottom: 10px;

    p {
      display: flex;
      flex-flow: column;
      justify-content: center;
      align-items: center;
    }

    input:not([id='rememberme']) {
      height: 40px;
      width: 200px;
      padding: 5px;
      font-size: 1.2rem;
      color: #000;
      margin: 10px 0;
    }

    .login-submit {
      input {
        height: inherit;
        background-color: #4a4a4a;
        border: none;
        margin: 20px 15px 5px 15px;
        padding: 20px;
        color: #fff;
        font-weight: bold;
        transition: 0.3s all;

        &:hover {
          cursor: pointer;
          background-color: lighten(#4a4a4a, 10%);
        }
      }
    }

    .login-remember label {
      font-size: 1rem;
    }

  }

  a {
    color: #ff884a;
    font-size: 1.5rem;
    font-weight: bold;
    text-decoration: none;
    transition: 0.3s all;

    &:hover {
      color: #dd521b;
    }
  }

}